import React from "react";
import { graphql } from "gatsby";
import {MainSectionWrapper} from "../layout";
import CommonHead from "../layout/components/Head";
import renderAst from "../layout/renderAst";
import {H2, H3, H4} from "../layout/components/Header";
import Link from "../layout/components/Link";

const PageTemplate = (props) => {
  if (!props.pageContext || !props.data) {
    return null;
  }

  const {data} = props;
  return (
    <MainSectionWrapper>

      <H2>Latest Additions</H2>

      {data?.allGoogleDocs?.edges?.map(({node}) => {
        const {childMarkdownRemark} = node;
        return (
          <Link key={node.id} href={node.url} useTheming={false}>
            <div className="StripeCard">
              <div className="StripeCardImage">
                {renderAst(childMarkdownRemark.htmlAst)}
              </div>
              <H3>{node.name}</H3>
              <H4>{node.price}</H4>
            </div>
          </Link>
        );
      })}

    </MainSectionWrapper>
  )
};

export function Head(props) {
  return <CommonHead {...props} />;
}

export const query = graphql`
  query PetitsAnimaux {
    allGoogleDocs(filter: {directory: {eq: "petitsanimaux"}}) {
      edges {
        node {
          id
          name
          url
          price
          childMarkdownRemark {
            htmlAst
          }
        }
      }
    }
  }
`;

export default PageTemplate;